<script lang="ts">
	import { afterSleep, box, mergeProps } from "svelte-toolbelt";
	import type { AlertDialogContentProps } from "../types.js";
	import DismissibleLayer from "../../utilities/dismissible-layer/dismissible-layer.svelte";
	import EscapeLayer from "../../utilities/escape-layer/escape-layer.svelte";
	import FocusScope from "../../utilities/focus-scope/focus-scope.svelte";
	import PresenceLayer from "../../utilities/presence-layer/presence-layer.svelte";
	import TextSelectionLayer from "../../utilities/text-selection-layer/text-selection-layer.svelte";
	import { createId } from "../../../internal/create-id.js";
	import { noop } from "../../../internal/noop.js";
	import ScrollLock from "../../utilities/scroll-lock/scroll-lock.svelte";
	import { DialogContentState } from "../../dialog/dialog.svelte.js";
	import { shouldEnableFocusTrap } from "../../../internal/should-enable-focus-trap.js";

	const uid = $props.id();

	let {
		id = createId(uid),
		children,
		child,
		ref = $bindable(null),
		forceMount = false,
		interactOutsideBehavior = "ignore",
		onCloseAutoFocus = noop,
		onEscapeKeydown = noop,
		onOpenAutoFocus = noop,
		onInteractOutside = noop,
		preventScroll = true,
		trapFocus = true,
		restoreScrollDelay = null,
		...restProps
	}: AlertDialogContentProps = $props();

	const contentState = DialogContentState.create({
		id: box.with(() => id),
		ref: box.with(
			() => ref,
			(v) => (ref = v)
		),
	});

	const mergedProps = $derived(mergeProps(restProps, contentState.props));
</script>

<PresenceLayer
	{forceMount}
	open={contentState.root.opts.open.current || forceMount}
	ref={contentState.opts.ref}
>
	{#snippet presence()}
		<FocusScope
			ref={contentState.opts.ref}
			loop
			{trapFocus}
			enabled={shouldEnableFocusTrap({
				forceMount,
				present: contentState.root.opts.open.current,
				open: contentState.root.opts.open.current,
			})}
			onCloseAutoFocus={(e) => {
				onCloseAutoFocus(e);
				if (e.defaultPrevented) return;
				afterSleep(0, () => contentState.root.triggerNode?.focus());
			}}
			onOpenAutoFocus={(e) => {
				onOpenAutoFocus(e);
				if (e.defaultPrevented) return;
				e.preventDefault();
				afterSleep(0, () => contentState.opts.ref.current?.focus());
			}}
		>
			{#snippet focusScope({ props: focusScopeProps })}
				<EscapeLayer
					{...mergedProps}
					enabled={contentState.root.opts.open.current}
					ref={contentState.opts.ref}
					onEscapeKeydown={(e) => {
						onEscapeKeydown(e);
						if (e.defaultPrevented) return;
						contentState.root.handleClose();
					}}
				>
					<DismissibleLayer
						{...mergedProps}
						ref={contentState.opts.ref}
						enabled={contentState.root.opts.open.current}
						{interactOutsideBehavior}
						onInteractOutside={(e) => {
							onInteractOutside(e);
							if (e.defaultPrevented) return;
							contentState.root.handleClose();
						}}
					>
						<TextSelectionLayer
							{...mergedProps}
							ref={contentState.opts.ref}
							enabled={contentState.root.opts.open.current}
						>
							{#if child}
								{#if contentState.root.opts.open.current}
									<ScrollLock {preventScroll} {restoreScrollDelay} />
								{/if}
								{@render child({
									props: mergeProps(mergedProps, focusScopeProps),
									...contentState.snippetProps,
								})}
							{:else}
								<ScrollLock {preventScroll} />
								<div {...mergeProps(mergedProps, focusScopeProps)}>
									{@render children?.()}
								</div>
							{/if}
						</TextSelectionLayer>
					</DismissibleLayer>
				</EscapeLayer>
			{/snippet}
		</FocusScope>
	{/snippet}
</PresenceLayer>
